.page {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.page__body {
  color: #060b35;
  margin: 0;
}

.card {
  box-sizing: border-box;
  background: #fdfdfd;
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__img {
  margin-inline: auto;
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 160px 134px;
  width: 100%;
  height: 134px;
  margin-bottom: 40px;
}

.card__infos {
  margin-bottom: 16px;
}

.card__infos-title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__infos-code {
  color: #616070;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__review-count {
  margin: 0;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  font-size: 16px;
  line-height: 18px;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.buy-link {
  box-sizing: border-box;
  color: #fdfdfd;
  text-transform: uppercase;
  background: #00abdb;
  border: 1px solid #00abdb;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .2s;
  display: flex;
}

.buy-link:hover {
  color: #00abdb;
  background: #fdfdfd;
}
